@import "../common/variable";
@import "../common/mixins";

.services {
  margin-bottom: r(248);

  &__scroll {
    padding-left: r(288);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  &__item {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin-right: r(256);
    max-width: r(640);
    width: 100%;

    &:not(&_active) > * {
      color: $greyMediumColor;
    }

    & > * {
      -webkit-transition: color 300ms ease;
      transition: color 300ms ease;
    }
  }

  &__images-wrapper {
    margin: r(96) 0 0 auto;
    max-width: r(1152);
    max-height: r(440);
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    position: relative;
    overflow: hidden;
  }

  &__image {
    width: 100%;
    height: 100%;
    position: absolute;
    -o-object-fit: cover;
       object-fit: cover;
  }
}
